import React, { useState, useRef } from "react";
import "./ContactByMailForm.css";
import { style } from "glamor";
import { trackContactForm, trackResumeDownload } from "../../utils/analytics";

function ContactByMailForm(props) {
  const theme = props.theme;
  const form = useRef();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState('');

  const styles = style({
    backgroundColor: `${theme.accentBright}`,
    ":hover": {
      boxShadow: `0 5px 15px ${theme.accentBright}`,
    },
  });

  const sendEmail = (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    setSubmitStatus('');

    // Get form data
    const formData = new FormData(form.current);
    const name = formData.get('user_name');
    const email = formData.get('user_email');
    const message = formData.get('message');

    // Option 1: Use Formspree for direct email sending
    if (true) { // Formspree is now enabled
      fetch('https://formspree.io/f/xldpgool', {
        method: 'POST',
        body: formData,
        headers: {
          'Accept': 'application/json'
        }
      })
      .then((response) => {
          if (response.ok) {
            console.log('Email sent successfully');
            setSubmitStatus('success');
            setIsSubmitting(false);
            form.current.reset();
            // Track successful form submission
            trackContactForm('success');
          } else {
            throw new Error('Failed to send email');
          }
      })
      .catch((error) => {
          console.error('Email send failed:', error);
          setSubmitStatus('error');
          setIsSubmitting(false);
          // Track failed form submission
          trackContactForm('error');
      });
    } else {
      // Option 2: Fallback to mailto (opens default email client)
      const subject = encodeURIComponent(`Portfolio Contact from ${name}`);
      const body = encodeURIComponent(`Name: ${name}\nEmail: ${email}\n\nMessage:\n${message}`);
      const mailtoLink = `mailto:varshabhat42@gmail.com?subject=${subject}&body=${body}`;
      
      // Simulate processing time
      setTimeout(() => {
        window.location.href = mailtoLink;
        setSubmitStatus('success');
        setIsSubmitting(false);
        form.current.reset();
      }, 1000);
    }
  };

  return (
    <div className="contact-form-container">
      <form ref={form} id="contact-form" className="contact-form" onSubmit={sendEmail}>
        <div className="contact-form-row">
          <input
            type="text"
            name="user_name"
            placeholder="Name"
            className="contact-form-input"
            required
          />
          <input
            type="email"
            name="user_email"
            placeholder="Email"
            className="contact-form-input"
            required
          />
        </div>
        <textarea
          name="message"
          placeholder="Message"
          className="contact-form-textarea"
          required
        ></textarea>
        
        {submitStatus === 'success' && (
          <div className="submit-message success">
            Message sent successfully! I'll get back to you soon!
          </div>
        )}
        
        {submitStatus === 'error' && (
          <div className="submit-message error">
            Failed to send message. Please try again or contact me directly.
          </div>
        )}

      </form>
      <div className="contact-actions">
        <button 
          {...styles} 
          type="submit" 
          form="contact-form" 
          className="contact-form-button general-btn"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Sending...' : 'Send Message'}
        </button>
        <a 
          {...props.styles} 
          className="general-btn" 
          href="VarshaResume.pdf" 
          target="_blank" 
          rel="noopener noreferrer"
          onClick={() => trackResumeDownload()}
        >
          See my Resume
        </a>
      </div>
    </div>
  );
}

export default ContactByMailForm;
