function SkillSection(props) {
  const theme = props.theme;
  
  // Define the card style similar to DegreeCard
  const skillCardStyle = style({
    borderBottom: `1px solid ${theme.accentColor}`,
    borderLeft: `1px solid ${theme.accentColor}`,
    borderRight: `1px solid ${theme.accentColor}`,
    borderTop: `1px solid ${theme.accentColor}`,
    borderRadius: "7px",
    width: "49%",
    margin: "0",
    padding: "20px 20px 15px 20px", // Reduced bottom padding
    boxShadow: `0px 1px 5px ${theme.accentColor}`,
    transition: "all 0.2s ease-in-out",
    ":hover": {
      color: "rgba(255, 255, 255, 1)",
      boxShadow: `0 5px 15px ${theme.accentColor}`,
    },
    "@media (max-width: 1024px) and (min-width: 769px)": {
      width: "49%", // Keep two cards per row for tablets and medium laptops
      margin: "0",
      padding: "18px 18px 12px 18px",
    },
    "@media (max-width: 768px)": {
      width: "100%", // Single column on mobile
      margin: "0 0 20px 0",
      padding: "15px 15px 10px 15px", // Reduced mobile padding
    },
  });

  return (
    <div className="skills-container">
      <div className="skills-grid">
        {skills.data.map((skill, index) => {
          return (
            <Fade bottom duration={1000 + index * 200} distance="20px" key={index}>
              <div {...skillCardStyle} className="skills-main-div">
                <div className="skills-content">
                  <h1 className="skills-heading" style={{ color: theme.text }}>
                    {skill.title}
                  </h1>
                  
                  {/* <div className="software-skills-wrapper">
                    <SoftwareSkill logos={skill.softwareSkills} />
                  </div> */}
                  
                  <div className="skills-description">
                    {skill.skills.map((skillSentence, skillIndex) => {
                      // Check if skillSentence is a string or an object with links
                      if (typeof skillSentence === "string") {
                        return (
                          <p
                            key={skillIndex}
                            className="subTitle skills-text"
                            style={{ color: theme.secondaryText }}
                          >
                            {skillSentence}
                          </p>
                        );
                      } else {
                        // Handle skills with embedded links
                        return (
                          <p
                            key={skillIndex}
                            className="subTitle skills-text"
                            style={{ color: theme.secondaryText }}
                          >
                            {skillSentence.text}
                            {skillSentence.links && skillSentence.links.map((link, linkIndex) => (
                              <a
                                key={linkIndex}
                                href={link.url}
                                target="_blank"
                                rel="noopener noreferrer"
                                style={{
                                  color: theme.accentColor,
                                  textDecoration: "underline",
                                  textDecorationColor: theme.accentColor,
                                }}
                              >
                                {link.text}
                              </a>
                            ))}
                            {skillSentence.continuation}
                            {skillSentence.links2 && skillSentence.links2.map((link, linkIndex) => (
                              <a
                                key={linkIndex}
                                href={link.url}
                                target="_blank"
                                rel="noopener noreferrer"
                                style={{
                                  color: theme.accentColor,
                                  textDecoration: "underline",
                                  textDecorationColor: theme.accentColor,
                                }}
                              >
                                {link.text}
                              </a>
                            ))}
                            {skillSentence.continuation2}
                            {skillSentence.links3 && skillSentence.links3.map((link, linkIndex) => (
                              <a
                                key={linkIndex}
                                href={link.url}
                                target="_blank"
                                rel="noopener noreferrer"
                                style={{
                                  color: theme.accentColor,
                                  textDecoration: "underline",
                                  textDecorationColor: theme.accentColor,
                                }}
                              >
                                {link.text}
                              </a>
                            ))}
                            {skillSentence.ending}
                          </p>
                        );
                      }
                    })}
                  </div>
                </div>
              </div>
            </Fade>
          );
        })}
      </div>
    </div>
  );
}

export default SkillSection;
