import React, { useState, useEffect } from "react";
import "./Recommendations.css";
import { recommendations, socialMediaLinks } from "../../portfolio";
import { Fade } from "react-reveal";
import { style } from "glamor";

export default function Recommendations(props) {
  const theme = props.theme;
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedRecommendation, setSelectedRecommendation] = useState(null);
  
  const cardStyle = style({
    border: `1px solid ${theme.accentColor}`,
    boxShadow: `0px 1px 5px ${theme.accentColor}`,
  });

  const openModal = (recommendation) => {
    setSelectedRecommendation(recommendation);
    setIsModalOpen(true);
    // Prevent body scroll when modal is open
    document.body.style.overflow = 'hidden';
  };

  const handleCardClick = (recommendation, e) => {
    // Only allow card click on desktop
    if (window.innerWidth >= 1025) {
      openModal(recommendation);
    }
  };

  const handleButtonClick = (recommendation, e) => {
    e.stopPropagation();
    openModal(recommendation);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedRecommendation(null);
    // Restore body scroll when modal is closed
    document.body.style.overflow = 'unset';
  };

  // Cleanup effect to restore scroll if component unmounts with modal open
  useEffect(() => {
    return () => {
      if (isModalOpen) {
        document.body.style.overflow = 'unset';
      }
    };
  }, [isModalOpen]);


  
  // Convert hex to RGB for CSS variables
  const hexToRgb = (hex) => {
    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
      r: parseInt(result[1], 16),
      g: parseInt(result[2], 16),
      b: parseInt(result[3], 16)
    } : null;
  };

  const rgb = hexToRgb(theme.accentColor);

  return (
    <div 
      className="main" 
      id="recommendations"
      style={{
        '--accent-color': theme.accentColor,
        '--accent-color-alpha': `${theme.accentColor}50`,
        '--accent-color-rgb': rgb ? `${rgb.r}, ${rgb.g}, ${rgb.b}` : '64, 224, 208'
      }}
    >
      <div className="recommendations-header-div">
        <Fade bottom duration={2000} distance="20px">
          <a 
            href={socialMediaLinks.linkedin} 
            target="_blank" 
            rel="noopener noreferrer"
            className="recommendations-header-link"
            style={{ 
              color: theme.text,
              '--text-color': theme.text,
              '--accent-color': theme.accentColor
            }}
          >
            <h1 className="recommendations-header">
              What People Say
            </h1>
          </a>
        </Fade>
      </div>
      <div className="recommendations-container">
        <div className="recommendations-scroll">
          {recommendations.data.map((recommendation, index) => {
            return (
              <Fade bottom duration={1000 + index * 200} distance="20px" key={index}>
                <div 
                  {...cardStyle} 
                  className="recommendation-card"
                  onClick={(e) => handleCardClick(recommendation, e)}
                  style={{ position: 'relative' }}
                >
                  <div className="recommendation-content">
                    <h2 className="recommendation-name" style={{ color: theme.text }}>
                      {recommendation.name}
                    </h2>
                    <h3 className="recommendation-title" style={{ color: "#FFFFFF90" }}>
                      {recommendation.title}
                    </h3>
                    <p className="recommendation-message" style={{ color: theme.secondaryText }}>
                      {recommendation.message}
                    </p>
                  </div>
                  
                  {/* See More button for mobile/tablet */}
                  <button 
                    className="see-more-button-rec"
                    onClick={(e) => handleButtonClick(recommendation, e)}
                    style={{ 
                      backgroundColor: theme.accentColor,
                      borderColor: theme.accentColor
                    }}
                  >
                    See More →
                  </button>
                </div>
              </Fade>
            );
          })}
        </div>
      </div>

      {/* Modal Overlay */}
      {isModalOpen && selectedRecommendation && (
        <div className="modal-overlay" onClick={closeModal}>
          <div 
            className="modal-content" 
            onClick={(e) => e.stopPropagation()}
            style={{
              border: `2px solid ${theme.accentColor}`,
              boxShadow: `0 20px 60px rgba(0, 0, 0, 0.5), 0 0 30px ${theme.accentColor}40`
            }}
          >
            <button className="modal-close-button" onClick={closeModal}>
              ×
            </button>
            <div className="modal-recommendation">
              <h2 className="modal-recommendation-name" style={{ color: theme.text }}>
                {selectedRecommendation.name}
              </h2>
              <h3 className="modal-recommendation-title" style={{ color: "#FFFFFF90" }}>
                {selectedRecommendation.title}
              </h3>
              <div className="modal-recommendation-message" style={{ color: theme.secondaryText }}>
                {selectedRecommendation.message.split('\n').map((paragraph, index) => (
                  <p key={index} className="modal-paragraph">
                    {paragraph.trim()}
                  </p>
                ))}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}