import ReactGA from 'react-ga4';
import { settings } from '../portfolio';

export const initGA = () => {
  if (settings.googleTrackingID && settings.googleTrackingID !== 'G-XXXXXXXXXX') {
    ReactGA.initialize(settings.googleTrackingID, {
      debug: process.env.NODE_ENV === 'development', // Enable debug mode in development
    });
    console.log('Google Analytics initialized with ID:', settings.googleTrackingID);
  } else {
    console.warn('Google Analytics not initialized - please add your GA4 Measurement ID to portfolio.js');
  }
};

export const logPageView = () => {
  ReactGA.send({ 
    hitType: 'pageview', 
    page: window.location.pathname + window.location.search 
  });
};

export const logEvent = (action, category = 'User Interaction') => {
  ReactGA.event({
    category: category,
    action: action,
  });
};

export const logCustomEvent = (eventName, parameters = {}) => {
  ReactGA.event(eventName, parameters);
};

// Track specific portfolio interactions
export const trackPortfolioEvent = (action, section = '') => {
  ReactGA.event({
    category: 'Portfolio Interaction',
    action: action,
    label: section,
  });
};

// Track contact form submission
export const trackContactForm = (status) => {
  ReactGA.event({
    category: 'Contact Form',
    action: 'Form Submission',
    label: status, // 'success' or 'error'
  });
};

// Track resume download
export const trackResumeDownload = () => {
  ReactGA.event({
    category: 'Download',
    action: 'Resume Download',
    label: 'PDF Resume',
  });
};

// Track external link clicks
export const trackExternalLink = (url, label = '') => {
  ReactGA.event({
    category: 'External Link',
    action: 'Click',
    label: label || url,
  });
};