import React from "react";
import ProjectLanguages from "../projectLanguages/ProjectLanguages";
import ProjectLinks from "../ProjectLinks/ProjectLinks";
import "./ProjectCard.css";
import { Fade } from "react-reveal";
import { style } from "glamor";

export default function ProjectCard({ repo, theme, onProjectClick }) {
  console.log(repo);
  // function openRepoinNewTab(url) {
  //   var win = window.open(url, "_blank");
  //   win.focus();
  // }

  const handleCardClick = (e) => {
    // Only allow card click on desktop
    if (window.innerWidth >= 1025 && onProjectClick) {
      onProjectClick(repo);
    }
  };

  const handleButtonClick = (e) => {
    e.stopPropagation();
    if (onProjectClick) {
      onProjectClick(repo);
    }
  };

  const styles = style({
    display: "flex",
    flexDirection: "column",
    background: "transparent",
    borderRadius: "7px",
    padding: "20px",
    transition: "all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1) !important",
    cursor: "pointer !important",
    transform: "translateY(0) scale(1) !important",
    transformOrigin: "center !important",
    height: "100%",
    backgroundColor: theme.projectCard,
    position: "relative",
    "@media (min-width: 1025px)": {
      ":hover": {
        transform: "translateY(-4px) scale(1.02) !important",
        boxShadow: `0 6px 20px ${theme.accentBright}66 !important`,
      },
      ":active": {
        transform: "translateY(-1px) scale(1.01) !important",
      },
    },
  });

  return (
    <div>
      <Fade bottom duration={2000} distance="40px">
        <div
          {...styles}
          key={repo.id}
          onClick={handleCardClick}
          style={{ backgroundColor: theme.projectCard }}
        >
          <div className="repo-name-div">
            <p className="repo-name" style={{ color: theme.text, fontWeight: '800' }}>
              {repo.name}
            </p>
            {repo.subtitle && (
              <p className="repo-subtitle" style={{ color: theme.secondaryText, fontWeight: '600' }}>
                {repo.subtitle}
              </p>
            )}
          </div>
          <div className="repo-description" style={{ color: theme.text, flexGrow: 1 }}>
            <p className="repo-summary">
              {repo.summary}
            </p>
          </div>
          <div className="flexDiv">
            {/* <div className="repo-details Leftitem">
              <ProjectLanguages logos={repo.languages} />
            </div> */}
            {/* <div className="repo-details Rightitem">
              <ProjectLinks logos={repo.links} />
            </div> */}
          </div>
          
          {/* See More button for mobile/tablet */}
          <button 
            className="see-more-button"
            onClick={handleButtonClick}
            style={{ 
              backgroundColor: theme.accentColor,
              borderColor: theme.accentColor
            }}
          >
            See More →
          </button>
        </div>
      </Fade>
    </div>
  );
}
