import React, { useState, useEffect } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import ProjectCard from "../../components/ProjectCard/ProjectCard";
import { Fade } from "react-reveal";
import { projectsHeader, projects } from "../../portfolio.js";
import "./Projects.css";
import ProjectsImgGirl from "./projects_image_girl.png";
import ProjectsImgGirl2 from "./projects_image_girl_2.png";
import { style } from "glamor";

function Projects(props) {
  const theme = props.theme;
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedProject, setSelectedProject] = useState(null);

  const styles = style({
    backgroundColor: `${theme.accentBright}`,
    ":hover": {
      boxShadow: `0 5px 15px ${theme.accentBright}`,
    },
  });

  const openModal = (project) => {
    setSelectedProject(project);
    setIsModalOpen(true);
    // Prevent body scroll when modal is open
    document.body.style.overflow = 'hidden';
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedProject(null);
    // Restore body scroll when modal is closed
    document.body.style.overflow = 'unset';
  };

  // Cleanup effect to restore scroll if component unmounts with modal open
  useEffect(() => {
    return () => {
      if (isModalOpen) {
        document.body.style.overflow = 'unset';
      }
    };
  }, [isModalOpen]);

  return (
    <div className="projects-main">
      <Header theme={theme} setTheme={props.setTheme} />
      <div className="basic-projects">
        <Fade bottom duration={2000} distance="40px">
          <div className="projects-heading-div">
            <div className="projects-heading-img-div">
              {/* <ProjectsImg theme={theme} /> */}
              <img src = {ProjectsImgGirl2} alt="Woman developing projects"/>
            </div>
            <div className="projects-heading-text-div">
              <h1
                className="projects-heading-text"
                style={{ color: theme.text }}
              >
                {projectsHeader.title}
              </h1>
              <p
                className="projects-header-detail-text subTitle"
                style={{ color: theme.secondaryText }}
              >
                {projectsHeader["description"]}
              </p>
            </div>
          </div>
        </Fade>
      </div>
      <div className="repo-cards-div-main">
        {projects.data.map((repo) => {
          return <ProjectCard repo={repo} theme={theme} onProjectClick={openModal} />;
        })}
      </div>
      <br />
      <br />
      {/* <br />
      <a
        {...styles}
        className="general-btn"
        href="https://github.com/harikanani"
      >
        More Projects (Github)
      </a>
      <br />
      <br /> */}
      
      {/* Modal Overlay */}
      {isModalOpen && selectedProject && (
        <div className="modal-overlay" onClick={closeModal}>
          <div 
            className="modal-content" 
            onClick={(e) => e.stopPropagation()}
            style={{
              border: `2px solid ${theme.accentColor}`,
              boxShadow: `0 20px 60px rgba(0, 0, 0, 0.5), 0 0 30px ${theme.accentColor}40`
            }}
          >
            <button className="modal-close-button" onClick={closeModal}>
              ×
            </button>
            <div className="modal-project">
              <h2 className="modal-project-name" style={{ color: theme.text }}>
                {selectedProject.name}
              </h2>
              {selectedProject.subtitle && (
                <h3 className="modal-project-subtitle" style={{ color: "#FFFFFF90" }}>
                  {selectedProject.subtitle}
                </h3>
              )}
              {selectedProject.url && (
                <div className="modal-project-url">
                  <a 
                    href={selectedProject.url} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    style={{ color: theme.accentColor }}
                  >
                    🔗 View Project
                  </a>
                </div>
              )}
              <div className="modal-project-description" style={{ color: theme.text }}>
                <ul>
                  {selectedProject.description.map((item, index) => (
                    <li key={index} className="modal-description-item">
                      {item}
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </div>
        </div>
      )}
      
      <Footer theme={props.theme} onToggle={props.onToggle} />
    </div>
  );
}

export default Projects;
